.modal {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 100;
  overflow: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.modal_title {
  display: flex;
  align-items: center;
  min-height: 64px;
  text-align: left;
}
