.header {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 1;
  width: 100%;
  box-shadow: 0 24px 32px rgba(0, 0, 0, 0.04), 0 16px 24px rgba(0, 0, 0, 0.04), 0 4px 8px rgba(0, 0, 0, 0.04), 0 0px 1px rgba(0, 0, 0, 0.04);
  background-color: #1C1C21;
}

.header__inner {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 1280px;
  margin: 0 auto;
}

.header__logo-wr {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate3d(-50%, -50%, 0);
}

.header__button:hover {
  text-decoration: none;
}

.header__button.active {
  pointer-events: none;
}

.header__button.active .text {
  color: #F2F2F3;
}

.header__button.active svg {
  fill: #F2F2F3;
}
